<script>
import userService from "../services/userService";

export default {
    data: () => ({
        valid: true,
        name: '',
        menu: false,
        menu_rg: false,
        date: null,
        date1: null,
        user: {
            complemento_trabalhador: {},
            dependente: {
                tipo1: 0,
                tipo2: 0,
                tipo3: 0,
                tipo4: 0,
                tipo5: 0,
                tipo6: 0,
                tipo7: 0,
                tipo8: 0,
                tipo9: 0,
                tipo10: 0,
                tipo11: 0,
            },
            identificacao: {},
            contato: {}
        },
        nameRules: [
            v => !!v || 'Requerido',
        ],
        email: '',
        emailRules: [
            v => !!v || 'E-mail is required',
            v => /.+@.+/.test(v) || 'E-mail must be valid'
        ],
        select: null,
        items: [{
                name: 'ANALFABETO',
                id: 1
            },
            {
                name: 'ATÉ A 4ª SÉRIE INCOMPLETA DO ENSINO FUNDAMENTAL (ANTIGO 1º GRAU OU PRIMÁRIO), OU QUE TENHA SE ALFABETIZADO SEM TER FREQUENTADO ESCOLA REGULAR',
                id: 2
            },
            {
                name: '4ª SÉRIE COMPLETA DO ENSINO FUNDAMENTAL (ANTIGO 1º GRAU OU GINÁSIO)',
                id: 3
            },
            {
                name: 'DA 5ª A 8ª SERIE DO ENSINO FUNDAMENTAL (ANTIGO 1º GRAU OU GINÁSIO)',
                id: 4
            },
            {
                name: 'ENSINO FUNDAMENTAL COMPLETO (ANTIGO 1º GRAU, PRIMÁRIO OU GINÁSIO)',
                id: 5
            },
            {
                name: 'ENSINO MÉDIO INCOMPLETO (ANTIGO 2º GRAU, SECUNDÁRIO OU COLEGIAL)',
                id: 6
            },
            {
                name: 'ENSINO MÉDIO COMPLETO (ANTIGO 2º GRAU, SECUNDÁRIO OU COLEGIAL)',
                id: 7
            },
            {
                name: 'EDUCAÇÃO SUPERIOR INCOMPLETA',
                id: 8
            },
            {
                name: 'EDUCAÇÃO SUPERIOR COMPLETA',
                id: 9
            },
            {
                name: 'PÓS-GRADUAÇÃO',
                id: 10
            },
            {
                name: 'MESTRADO',
                id: 11
            },
            {
                name: 'DOUTORADO',
                id: 12
            }
        ],
        checkbox: false
    }),
    computed: {
        computedDateFormatted() {
            //this.this.user.data_nascimento = this.formatDate(this.date);
            return this.formatDate(this.user.data_nascimento);
        }
    },
    watch: {
        menu(val) {
            val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'));
            this.user.data_nascimento = this.formatDate(this.date);
        },
        menu_rg(val) {
            val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'));
            this.user.identificacao.data_expedicao = this.formatDate(this.date1);
        },
        date(val) {
            this.this.user.data_nascimento = this.formatDate(this.date)
        },
        date1(val) {
            this.this.user.identificacao.data_expedicao = this.formatDate(this.date1)
        }
    },

    methods: {
        formatDate(date) {
            if (!date) return null

            const [year, month, day] = date.split('-')
            return `${day}/${month}/${year}`
        },
        parseDate(date) {
            if (!date) return null

            const [month, day, year] = date.split('/')
            return `${day.padStart(2, '0')}-${month.padStart(2, '0')}-${year}}`
        },
        save(date) {
            this.$refs.menu.save(date)
        },
        save2(date) {
            this.$refs.menu_rg.save(date)
        },
        submit() {
            userService
                .cadastro(this.user).then(r => {
                    if (r.status == 201) {
                        this.$router.push("/login");
                    }
                });
        },
        clear() {
            this.$refs.form.reset()
            this.$router.push("/login");
        }
    }
}
</script>
